import URL from "../../src/index.js";

export default function (test) {
  test("http://www.google.com/:x")
    .this(function () {
      let l = new URL("http://www.google.com/:x", { href: "http://www.google.com/cat" });
      return l;
    })
    .isDeepEqual(function () {
      return {
        schema: {
          origin   : "http://www.google.com",
          href     : "http://www.google.com/:x",
          pathname : "/:x",
          hash     : "",
          search   : ""
        },

        location: {
          origin   : "http://www.google.com",
          href     : "http://www.google.com/cat",
          pathname : "/cat",
          hash     : "",
          search   : ""
        },

        origin : {
          schema  : "http://www.google.com",
          value   : "http://www.google.com",
          isMatch : true
        },

        search : {
          schema  : {},
          keys    : [],
          isMatch : true
        },

        params : {
          schema  : [ ":x" ],
          value   : [ "cat" ],
          x       : "cat",
          isMatch : true
        },

        hash   : {
          schema  : "",
          value   : "",
          isMatch : true
        },

        isMatch : true
      };
    });

  test("http://www.google.com/:x/:y")
    .this(function () {
      let l = new URL("http://www.google.com/:x/:y", { href: "http://www.google.com/cats/dogs" });
      return l;
    })
    .isDeepEqual(function () {
      return {
        schema: {
          origin   : "http://www.google.com",
          href     : "http://www.google.com/:x/:y",
          pathname : "/:x/:y",
          hash     : "",
          search   : ""
        },

        location: {
          origin   : "http://www.google.com",
          href     : "http://www.google.com/cats/dogs",
          pathname : "/cats/dogs",
          hash     : "",
          search   : ""
        },

        origin : {
          schema  : "http://www.google.com",
          value   : "http://www.google.com",
          isMatch : true
        },

        search : {
          schema  : {},
          keys    : [],
          isMatch : true,
        },

        params : {
          schema  : [ ":x", ":y" ],
          value   : [ "cats", "dogs" ],
          x       : "cats",
          y       : "dogs",
          isMatch : true,
        },

        hash   : {
          schema  : "",
          value   : "",
          isMatch : true
        },

        isMatch : true
      };
    });

  test("http://www.google.com/:x/:y (isMatch: false)")
    .this(function () {
      let l = new URL("http://www.google.com/:x/:y", { href: "http://www.google.com/cats" });
      return l;
    })
    .isDeepEqual(function () {
      return {
        schema: {
          origin   : "http://www.google.com",
          href     : "http://www.google.com/:x/:y",
          pathname : "/:x/:y",
          hash     : "",
          search   : ""
        },

        location: {
          origin   : "http://www.google.com",
          href     : "http://www.google.com/cats",
          pathname : "/cats",
          hash     : "",
          search   : ""
        },

        origin : {
          schema  : "http://www.google.com",
          value   : "http://www.google.com",
          isMatch : true
        },

        search : {
          schema  : {},
          keys    : [],
          isMatch : true
        },

        params : {
          schema  : [ ":x", ":y" ],
          value   : [ "cats" ],
          x       : "cats",
          isMatch : false
        },

        hash   : {
          schema  : "",
          value   : "",
          isMatch : true
        },

        isMatch : false
      };
    });

  test("http://www.google.com/:x/:y (contains)")
    .this(function () {
      let l = new URL(null, "http://www.google.com/cats/dogs");
      return l.params.contains("/dogs") && l.params.contains("/dogs/") && !l.params.contains("/fish");
    })
    .isEqual(function () {
      return true;
    });
}


